import React from 'react';
import bg from '../../assets/images/bg@3x.png';
import x from '../../assets/images/x@3x.png';

class WuliNumberModal extends React.Component {
    constructor() {
        super();
        this.handleModalTouchMove = this.handleModalTouchMove.bind(this);
    }

    handleModalTouchMove(e) {
        e.preventDefault()
        e.stopPropagation();
    }

    componentDidMount() {
        document.getElementById('wulishadow').addEventListener("touchmove", (e) => {
            // 执行滚动回调
            this.handleModalTouchMove(e)
        }, {
            passive: false //  禁止 passive 效果
        })
    }

    render() {
        return (
            <div
                style={{
                    width: '100vw',
                    height: '100vh',
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    zIndex: 2,
                    backgroundColor: 'rgba(0,0,0,.5)',
                    display: this.props.show ? 'flex' : 'none',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    overflow: 'hidden',
                    touchAction: 'none',
                }}
                onTouchMove={this.handleModalTouchMove}
                id="wulishadow"
                onClick={this.props.close}
            >
                <div
                    style={{
                        display: this.props.show ? 'flex' : 'none',
                        width: 'calc(100vw - .6rem)',
                        height: '7.6rem',
                        background: `url(${bg}) center center / cover no-repeat`,
                    }}
                >
                </div>
                <div
                    style={{
                        width: '.6rem',
                        height: '.6rem',
                        background: `url(${x}) center center / cover no-repeat`,
                        marginTop: '.31rem',
                    }}
                    onClick={this.props.close}
                >
                </div>
            </div>
        )
    }
}

export default WuliNumberModal;


