import React from 'react';
import Modal from '../../common/Modal';
import WuliNumberModal from '../../common/WuliNumberModal';
import MembersUserModal from '../../common/MembersUserModal';
import axios from 'axios';
import md5 from 'blueimp-md5';
import { generateSign, app_key, app_secret, version, host } from '../../config';
import './members.css';

class Members extends React.Component {
    constructor() {
        super();
        this.state = {
            modalShow: false,
            price: 0,
            hint: '',
            hintText: '',
            wuliModalShow: false,
            uid: '',
            membersUserModalShow: false,
            userinfo: null,
            currentRechargeId: 1,
        }
        this.buyMembershipClick = this.buyMembershipClick.bind(this);
        this.modalClose = this.modalClose.bind(this);
        this.confirm = this.confirm.bind(this);
        this.wuliModalClose = this.wuliModalClose.bind(this)
        this.handleWuliModalShow = this.handleWuliModalShow.bind(this);
        this.handleUidChange = this.handleUidChange.bind(this);
        this.membersUserModalClose = this.membersUserModalClose.bind(this);
        this.membersUserModalConfirm = this.membersUserModalConfirm.bind(this);
    }
    buyMembershipClick() {
        this.setState({
            modalShow: true,
        })
    }
    modalClose() {
        this.setState({
            modalShow: false,
            hint: false,
            hintText: '',
        })
    }
    valid(uid) {
        let isOk = true;
        if(!uid){
            this.setState({
                hint: true,
                hintText: '请输入唔哩号'
            })
            isOk = false;
        }else if(uid.length <8) {
            this.setState({
                hint: true,
                hintText: '唔哩号不能小于8位，请检查后重新输入'
            })
            isOk = false;
        }else if(uid.length >= 8 && uid.length <= 10){
            this.setState({
                hint: false,
                hintText: '',
            })
        }
        else if(uid.length > 10) {
            this.setState({
                hint: true,
                hintText: '唔哩号不能大于10位，请检查后重新输入'
            })
            isOk = false;
        }
        return isOk;
    }
    confirm() {
        const {uid} = this.state;
        const isOk = this.valid(uid);
        if(!isOk) return false;
        const action = "WEB-FRIEND-DETAIL";
        const time = new Date().valueOf();
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token: '',
        })
        const sign = md5(orig_sign + time);
        axios({
            method: 'post',
            url: `${host}/service/relation/web-friend-detail.htm`,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action,
                app_key,
                app_secret,
                time,
                version,
                sign,
                friend_uid: uid,
                from,
            }
        })
        .then((response) => {
            const {data} = response;
            if(data.code === 200) {
                this.setState({
                    userinfo: data.data,
                    modalShow: false,
                    membersUserModalShow: true,
                })
            }else{
                this.setState({
                    hint: true,
                    hintText: data.message
                })
            }
        })
        .catch((e) => {
            console.log('error', e);
        })
        
        if(this.state.hint) {
            this.setState({
                hint: false,
                hintText: '',
            })
        }
    }
    wuliModalClose() {
        this.setState({
            wuliModalShow: false
        })
    }
    handleWuliModalShow() {
        this.setState({
            wuliModalShow: true,
        })
    }
    handleUidChange(e) {
        this.setState({
            uid: e.target.value,
        })
    }
    membersUserModalClose() {
        this.setState({
            membersUserModalShow: false,
        }) 
    }
    membersUserModalConfirm() {
        const {uid, currentRechargeId} = this.state;
        const action = "WEIXIN-OFFICE-TRADE-CONSTRUCT";
        const time = new Date().valueOf();
        const from = 'web';
        const type = 2;
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action,
            user_token: '',
        })
        const sign = md5(orig_sign + time);
        
        !(async () => {
            const code = this.props.match.params.code;
            console.log('this is code from url query', code)
            const wechatInfo = await axios.get(`http://test.guji-inc.com/api/auth?code=${code}`)
            return wechatInfo;
        })()
            .then(res => {
                console.log('---res---', res);
                const {openid} = res.data;
                console.log('openid', openid);
                return axios({
                    method: 'post',
                    url: `${host}/service/pay/weixin/office-trade-construct.htm`,
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    },
                    params: {
                        action,
                        app_key,
                        app_secret,
                        time,
                        version,
                        sign,
                        uid,
                        recharge_id: currentRechargeId,
                        open_id: 'oYkMFw94_xFhVFM1_GQ3pxzDSJds',
                        type,
                        from,
                    }
                })

            })
            .then((response) => {
                const {data} = response;
                if(data.code === 200) {
                    console.log('data.data111',data.data)
                    this.onBridgeReady(data.data);
                }else{
                    console.log('data.1111error----')
                    this.setState({
                        hint: true,
                        hintText: data.message
                    })
                }
            })
            .catch((e) => {
                console.log('error', e);
            })
    }
    handleMembersCardClick = (rechargeId) => (e) => {
        this.setState({
            currentRechargeId: rechargeId,
        })
    }
    componentDidMount() {
        document.height = document.body.clientHeight;
        document.body.style.height = document.body.clientHeight;
        console.log(document.height, document.body.style.height, document.body.clientHeight)
    }
    onBridgeReady(data) {
        const {
            appPackage,
            appid,
            noncestr,
            outTradeNo,
            partnerid,
            prepayid,
            sign,
            timestamp,
        } = data;
        window.WeixinJSBridge.invoke(
           'getBrandWCPayRequest', {
              appId: appid, 
              timeStamp: timestamp,     
              nonceStr: noncestr, 
              package: `prepay_id=${prepayid}`,     
              signType:'MD5', 
              paySign: sign 
           },
           (res) => {
               console.log('wechat pay res', res)
                if(res.err_msg === "get_brand_wcpay_request:ok" ){
                // 使用以上方式判断前端返回,微信团队郑重提示：
                //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
           } 
        }); 
     }
    
    render() {
        return (
            <div>
                <div className="members_wrap">
                    <div className="banner">
                    </div>
                    <div className="members_bg"></div>
                </div>
                <div 
                    className="members_button"
                    onClick={this.buyMembershipClick}
                >购买会员</div>
                <Modal
                    show={this.state.modalShow}
                    close={this.modalClose}
                    confirm={this.confirm}
                    hint={this.state.hint}
                    hintText={this.state.hintText}
                    handleWuliModalShow={this.handleWuliModalShow}
                    onUidChange={this.handleUidChange}
                />
                <WuliNumberModal
                    show={this.state.wuliModalShow}
                    close={this.wuliModalClose}
                />
                <MembersUserModal
                    show={this.state.membersUserModalShow}
                    close={this.membersUserModalClose}
                    userinfo={this.state.userinfo}
                    confirm={this.membersUserModalConfirm}
                    curId={this.state.currentRechargeId}
                    handleMembersCardClick={this.handleMembersCardClick}
                />
            </div>
        )
    }
}

export default Members;


