import React from 'react';

class Modal extends React.Component {
    constructor() {
        super()
        this.handleModalTouchMove = this.handleModalTouchMove.bind(this);
    }

    handleModalTouchMove(e) {
        e.preventDefault()
        e.stopPropagation();
    }

    componentDidMount() {
        document.getElementById('shadow').addEventListener("touchmove", (e) => {
            // 执行滚动回调
            this.handleModalTouchMove(e)
        }, {
            passive: false //  禁止 passive 效果
        })
    }
    render() {
        return  (
            <div
                style={{
                    width: '100vw',
                    height: '100vh',
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    zIndex: 2,
                    backgroundColor: 'rgba(0,0,0,.5)',
                    display: this.props.show ? 'flex' : 'none',
                    justifyContent: 'center',
                    alignItems: 'center',
                    overflow: 'hidden',
                    touchAction: 'none',
                }}
                onTouchMove={this.handleModalTouchMove}
                id="shadow"
                onClick={this.props.close}
            >
                <div
                    style={{
                        width: 'calc(100vw - .6rem)',
                        height: '3.9rem',
                        borderRadius: '10px', 
                        backgroundColor: '#2c2e3a',
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}
                    onClick={e=>e.stopPropagation()}
                >
                    <div
                        style={{
                            display: 'flex',
                            justfifyContent: 'space-between',
                            alignItems: 'center',
                            fontSize: '.28rem',
                            fontWeight: 'bold',
                            color: "#fff",
                        }}
                    >
                        请输入你的唔哩号：
                    </div>
                    <input 
                        style={{
                            width: '3.5rem',
                            height: '.8rem',
                            backgroundColor: '#262833',
                            borderRadius: '.1rem',
                            marginTop: '.2rem',
                            outline: 'none',
                            border: 'none',
                            paddingLeft: '.2rem',
                            paddingRight: '.2rem',
                            fontSize: '.28rem',
                            color: '#fff',
                        }}
                        type="number"
                        onChange={this.props.onUidChange}
                        
                    />
                    {
                        this.props.hint ? <div
                            style={{
                                color: '#ff345f',
                                fontSize: '.18rem',
                                marginTop: '.1rem',
                            }}
                        >
                            {this.props.hintText}
                        </div> : ''
                    }
                    <span
                        style={{
                            fontSize: '.18rem',
                            marginTop: '.2rem',
                            color: '#ffea00',
                            marginBottom: '.3rem',
                        }}
                        onClick={this.props.handleWuliModalShow}
                    >什么是唔哩号？</span>
                    <div
                        style={{
                            fontSize: '16px',
                        }}
                    >
                        {this.props.info}
                    </div>
                    <div
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            fontSize: '.28rem',
                            fontWeight: 'bold',
                            color: '#bebebe',
                        }}
                    >
                        <div
                            style={{
                                width: '2.45rem',
                                height: '.8rem',
                                borderRadius: '.4rem',
                                backgroundColor: '#262833',
                                textAlign: 'center',
                                lineHeight: '.8rem',
                                cursor: 'pointer',
                                userSelect: 'none',
                                marginRight: '.5rem',
                            }}
                            onClick={this.props.close}
                        >
                            取消
                        </div>
                        <div
                            style={{
                                width: '2.45rem',
                                height: '.8rem',
                                borderRadius: '.4rem',
                                backgroundColor: '#ffd900',
                                textAlign: 'center',
                                lineHeight: '.8rem',
                                cursor: 'pointer',
                                userSelect: 'none',
                                color: '#2c2e3a',
                            }}
                            onClick={this.props.confirm}
                        >
                            确定
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default Modal


