import React from 'react';
import { Link } from 'react-router-dom';
import Modal from '../../common/Modal';
import WuliNumberModal from '../../common/WuliNumberModal';
import UserModal from '../../common/UserModal';
import axios from 'axios';
import md5 from 'blueimp-md5';
import { generateSign, app_key, app_secret, version, host } from '../../config';
import './pay.css';

const Cube = ({diamond, rechargeId, price, cubeClick}) => {
    const img_dimond = require(`../../../assets/images/${rechargeId}@3x.png`);
    return <div className="cube"
        onClick={cubeClick(rechargeId)}
    >
        <div className="diamonds">
            <span className="diamond">

            </span>
            +{diamond}
        </div>
        <div
            style={{
                width: '2.17rem',
                height: '1.72rem',
                background: `url(${img_dimond}) center center / 100% no-repeat`,
            }}
        ></div>
        <div className="price">¥ {price}</div>    
    </div>
}
