import React from 'react';
import {connect} from 'react-redux';

const mapStateToProps = state => {
    const { user } = state;
    return { user };
}

const AuthContent = ({ user }) => (
    <div>User: {user}</div>
)

const Auth = connect(mapStateToProps)(AuthContent);

export default Auth;


