import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import store from './js/redux/store/index';

import AuthorizedRoute from './js/components/AuthorizedRoute';
import PrimaryLayout from './js/components/Main';
import UnauthorizedLayout from './js/components/Auth';

import './index.css';

ReactDOM.render(
    <Provider store={store}>
        <BrowserRouter>
            <Switch>
                <Route exact path="/auth" component={UnauthorizedLayout} />
                <AuthorizedRoute path="/" component={PrimaryLayout} />
            </Switch>
        </BrowserRouter>
    </Provider>, 
    document.getElementById('root')
);



