import React from 'react';

class UserModal extends React.Component {
    constructor() {
        super()
        this.handleModalTouchMove = this.handleModalTouchMove.bind(this);
    }

    handleModalTouchMove(e) {
        e.preventDefault()
        e.stopPropagation();
    }

    componentDidMount() {
        document.getElementById('shadow').addEventListener("touchmove", (e) => {
            // 执行滚动回调
            this.handleModalTouchMove(e)
        }, {
            passive: false //  禁止 passive 效果
        })
    }
    render() {
        const {userinfo} = this.props;
        const photo = userinfo && userinfo.photo
        return  (
            <div
                style={{
                    width: '100vw',
                    height: '100vh',
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    zIndex: 2,
                    backgroundColor: 'rgba(0,0,0,.5)',
                    display: this.props.show ? 'flex' : 'none',
                    justifyContent: 'center',
                    alignItems: 'center',
                    overflow: 'hidden',
                    touchAction: 'none',
                }}
                onTouchMove={this.handleModalTouchMove}
                id="shadow"
                onClick={this.props.close}
            >
                <div
                    style={{
                        width: 'calc(100vw - .6rem)',
                        height: '5.3rem',
                        borderRadius: '10px', 
                        backgroundColor: '#2c2e3a',
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'flex-start',
                        alignItems: 'center',
                    }}
                    onClick={e=>e.stopPropagation()}
                >
                    <div
                        style={{
                            display: 'flex',
                            justfifyContent: 'space-between',
                            alignItems: 'center',
                            fontSize: '.28rem',
                            fontWeight: 'bold',
                            color: "#fff",
                            marginTop: '.51rem',
                        }}
                    >
                        请确认账号：
                    </div>
                    <div
                        style={{
                            marginTop: '.4rem',
                            width: '1.4rem',
                            height: '1.4rem',
                            borderRadius: '.7rem',
                            border: '.02rem solid #fff',
                            boxSizing: 'border-box',
                            background: photo ? `url(${photo}) center center / cover no-repeat` : '',
                        }}                    
                    ></div>
                    <div
                        style={{
                            fontSize: '.32rem',
                            color: '#fff',
                            marginTop: '.1rem',
                        }}
                    >
                        {userinfo && userinfo.nickName}
                    </div>
                    <div
                        style={{
                            fontSize: '.24rem',
                            color: '#999',
                            marginTop: '.08rem',
                        }}
                    >
                        唔哩号：{userinfo && userinfo.uid}
                    </div>
                    <div
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            fontSize: '.28rem',
                            fontWeight: 'bold',
                            color: '#bebebe',
                            marginTop: '.4rem',
                        }}
                    >
                        <div
                            style={{
                                width: '2.45rem',
                                height: '.8rem',
                                borderRadius: '.4rem',
                                backgroundColor: '#262833',
                                textAlign: 'center',
                                lineHeight: '.8rem',
                                cursor: 'pointer',
                                userSelect: 'none',
                                marginRight: '.5rem',
                            }}
                            onClick={this.props.close}
                        >
                            取消
                        </div>
                        <div
                            style={{
                                width: '2.45rem',
                                height: '.8rem',
                                borderRadius: '.4rem',
                                backgroundColor: '#ffd900',
                                textAlign: 'center',
                                lineHeight: '.8rem',
                                cursor: 'pointer',
                                userSelect: 'none',
                                color: '#2c2e3a',
                            }}
                            onClick={this.props.confirm}
                        >
                            确定
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


export default UserModal


